/*********************************/
@import 'variables';
@import 'theme';
@import 'animation';
@import 'features';
@import 'fonts';

/* Begin Reset */

body {
  margin: 0px;
  padding: 0px;
  width: 100%;
  overflow: hidden;
}

ul {
  list-style-type: none;
  padding: 0px;
  margin: 0px;
}

a {
  &:link, &:visited {
    color: $gray-darker;
    text-decoration: none;
  }
}

/* End Reset */

html {
  visibility: hidden;
}

#check, #old, .theme, .setting {
  display: none;
}

#video {
  visibility: hidden;
  position: fixed;
  z-index: -10;
  left: 0;
  top: 0;
}

/*********************************/

/*HOME STYLE*/

/*********************************/

#new, #old {
  position: fixed;
  max-width: 100%;
  top: 50%;
  left: 50%;
  overflow: hidden;
}

.theme {
  font-family: $font;
  opacity: 0.9;
  position: absolute;
  top: 92px;
  bottom: 40px !important;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: auto;
  z-index: 1;
  .list-item {
    padding: 30px;
  }
}

#search-package {
  position: fixed;
  top: 105px;
  left: 50%;
  margin-left: -157px;
}

#package-list {
  margin-top: 60px;
}

.animated-search-filter .hidden {
    opacity: 0;
    pointer-events: none;
    position: absolute;
}
// .animated-search-filter > * {
//     transition: .2s;
// }

/*********************************/

/* BARS STYLE */

/*********************************/

header, footer {
  position: fixed;
  width: 100%;
  z-index: 2;
}

nav {
  position: inherit;
  top: 0;
  width: 100%;
}

#title {
  font-family: $font;
  color: #eee;
  font-weight: bold;
  text-align: center;
  margin: 10px;
}

.tab {
  position: inherit;
  display: table;
  width: 100%;
}

.tab-header {
  top: 42px;
  height: 50px;
}

.tab-footer {
  bottom: 0;
  height: 40px;
  a {
    display: table-cell;
    text-align: center;
    font-size: 25px;
    padding: 0;
    width: 16.5%;
  }
  img {
    vertical-align: middle;
  }
}

.active {
  background-color: $gray-dark;
}

.nav {
  height: inherit;
  cursor: pointer;
  img {
    opacity: 0.5;
    width: 50px;
    transition: all 0.3s ease;
    vertical-align: middle;
    &:hover {
      opacity: 1;
    }
  }
  .size:hover {
    width: 55px;
  }
}

.setting {
  position: fixed;
  text-align: center;
  bottom: 40px;
  left: 0px;
  right: 0px;
  height: 60px;
  background-color: $gray-darker;
  white-space: nowrap;
  overflow: hidden;
}

.setting-2 {
  height: 85px;
}

.setting-3 {
  height: 90px;
}

.setting img {
  margin-left: 3px;
  margin-top: 5px;
  opacity: 0.75;
  transition: all 0.3s ease;
}

.circular {
  width: 50px;
  height: 50px;
  border-radius: 45%;
  -webkit-border-radius: 45%;
  -moz-border-radius: 45%;
}

.filter-img {
  width: 50px;
  height: 50px;
  -webkit-filter: grayscale(0);
	filter: grayscale(0);
	-webkit-transition: .3s ease-in-out;
	transition: .3s ease-in-out;
  opacity: 1 !important;
}

.filter-img:hover {
  -webkit-filter: grayscale(100%);
	filter: grayscale(100%);
}

.label {
  position: absolute;
  margin-top: 48px;
  margin-left: -65px;
  width: 80px;
  text-align: center;
  font-family: $font;
  color: $gray-light;
  z-index: 2;
  cursor: pointer;
}

.label-2 {
  margin-top: 58px;
}

.square {
  width: 50px;
  height: 50px;
}

.setting {
  img:hover {
    opacity: 1;
  }
  li {
    display: inline;
    margin: 0px 12px;
  }
  [type="range"] {
    margin-top: 20px;
    width: 80%;
  }
}

#rgb [type="range"] {
  width: 25%;
}

.toright {
  margin-left: 36px !important;
}

#filters {
  display: block;
}

.back {
  height: 60px;
  float: left;
  border-right: 1px solid $gray-dark;
}

.back-icon {
  width: 35px;
  color: #929292;
  margin-top: 10.5px !important;
  opacity: 0.5;
  transition: all 0.3s ease;
}

.back:hover .back-icon {
  opacity: 1;
}

#back, #c-back {
  display: none;
  position: fixed;
  bottom: 40px;
  left: 0px;
  margin-top: 6px;
  background-color: $gray-darker;
}

#back .back-icon {
  margin-top: 12px !important;
}

.list-item, .nav img, .setting img, .tab-footer a, .back {
  cursor: pointer;
}

/*********************************/

/* BUTTONS STYLE */

/*********************************/

.btn {
  cursor: pointer;
  width: 120px;
  height: 30px;
  font-family: $font;
  font-size: 14px;
  color: #eee;
  background-color: $gray-dark;
  border: 2px solid $gray;
  margin-top: 12px;
  margin-left: -40px;
  &:active {
    margin-top: 14px;
  }
}


/*********************************/

/* RANGE STYLE */

/*********************************/

input[type="range"] {
  -webkit-appearance: none !important;
  -moz-appearance: none !important;
  //appearance: none !important;
  width: 100%;
  height: 15px;
  background-color: $gray-dark;
  border: 1px solid $gray-dark;
  border-radius: 10px;
  margin: auto;
  margin-top: 22px;
  transition: all 0.3s ease;
  &:hover {
    background-color: $gray;
    border: 1px solid $gray;
  }
  &::-webkit-slider-thumb {
    -webkit-appearance: none !important;
    //appearance: none !important;
    width: 20px;
    height: 20px;
    background-color: $gray-light;
    box-shadow: 0px 0px 3px $gray-dark;
    border-radius: 30px;
    transition: all 0.5s ease;
  }
  &::-moz-range-thumb {
    -moz-appearance: none !important;
    //appearance: none !important;
    width: 20px;
    height: 20px;
    background-color: $gray-light;
    box-shadow: 0px 0px 3px $gray-dark;
    border-radius: 30px;
    transition: all 0.5s ease;
  }
  &::-webkit-slider-thumb:hover, &::-moz-range-thumb:hover {
    background-color: #aaa;
  }
}

/*********************************/

/* SEARCH INPUT STYLE */

/*********************************/

.search-box {
  -webkit-transition: width 0.6s, border-radius 0.6s, background 0.6s, box-shadow 0.6s;
  transition: width 0.6s, border-radius 0.6s, background 0.6s, box-shadow 0.6s;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  border: none;
  cursor: pointer;
  background: #ebebeb;
  &:hover {
    color: white;
    background: #c8c8c8;
    box-shadow: 0 0 0 5px #3d4752;
  }
  &:focus {
    -webkit-transition: width 0.6s cubic-bezier(0, 1.22, 0.66, 1), border-radius 0.6s, background 0.6s;
    transition: width 0.6s cubic-bezier(0, 1.22, 0.66, 1), border-radius 0.6s, background 0.6s;
    border: none;
    outline: none;
    box-shadow: none;
    padding-left: 15px;
    cursor: text;
    width: 300px;
    border-radius: auto;
    background: #ebebeb;
    color: black;
  }
  &:not(:focus) {
    text-indent: -5000px;
  }
}

.search-icon {
  position: relative;
  left: -36px;
  bottom: -11px;
  cursor: pointer;
}

/*********************************/

/* POPUP STYLE */

/*********************************/

.overlay {
  font-family: $font;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  transition: opacity 500ms;
  visibility: hidden;
  opacity: 0;
  z-index: 999;
  &:target {
    visibility: visible;
    opacity: 1;
  }
}

.popup {
  margin: 70px auto;
  padding: 20px;
  background: #fff;
  border-radius: 5px;
  width: 40%;
  position: relative;
  transition: all 5s ease-in-out;
  h2 {
    margin-top: 0;
    color: #333;
    font-family: $font;
  }
  .close {
    position: absolute;
    top: 20px;
    right: 30px;
    transition: all 200ms;
    font-size: 30px;
    font-weight: bold;
    text-decoration: none;
    cursor: pointer;
    color: #333;
    &:hover {
      color: #ec5f8b;
    }
  }
  .dim, .pos, .ext {
    transition: all 200ms;
    font-size: 20px;
    font-weight: normal;
    text-decoration: none;
    color: #333;
    margin-right: 15px;
    cursor: pointer;
    &:hover {
      color: #ec5f8b;
    }
  }
  .ext{
    font-weight: bold;
  }
  .active-link{
    color: #ec5f8b;
  }
  .content {
    max-height: 30%;
    overflow: auto;
  }
}

@media screen and (max-width: 700px) {
  .box, .popup {
    width: 80%;
  }
}

/*********************************/

/* SHARE BUTTONS */

/*********************************/

.share-btn-icon {
  font-family: 'share-buttons';
  //speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.share-btn-email .share-btn-icon:before {
  content: "\e945";
}

.share-btn-more .share-btn-icon:before {
  content: "\ea82";
}

.share-btn-googleplus .share-btn-icon:before {
  content: "\ea88";
}

.share-btn-facebook .share-btn-icon:before {
  content: "\ea8c";
}

.share-btn-twitter .share-btn-icon:before {
  content: "\ea91";
}

.share-btn-github .share-btn-icon:before {
  content: "\eab4";
}

.share-btn-tumblr .share-btn-icon:before {
  content: "\eabb";
}

.share-btn-reddit .share-btn-icon:before {
  content: "\eac7";
}

.share-btn-linkedin .share-btn-icon:before {
  content: "\eac8";
}

.share-btn-delicious .share-btn-icon:before {
  content: "\eacc";
}

.share-btn-stumbleupon .share-btn-icon:before {
  content: "\eace";
}

.share-btn-pinterest .share-btn-icon:before {
  content: "\ead0";
}

.share-btn {
  box-sizing: border-box;
  * {
    box-sizing: border-box;
    box-sizing: border-box;
    &:before, &:after {
      box-sizing: border-box;
    }
  }
  box-sizing: border-box;
  position: relative;
  display: inline-block;
  height: 24px;
  margin: 5px;
  padding: 2px 8px;
  line-height: 1.53;
  letter-spacing: .04em;
  vertical-align: top;
  font-size: 12px;
  font-weight: bold;
  font-family: $font;
  color: #111;
  background: #e0e0e0;
  border: 1px solid #c7c7c7;
  border-radius: 2px;
  text-decoration: none;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  &:hover, &:focus {
    background: #d3d3d3;
    border-color: #bababa;
    text-decoration: none;
    color: #111;
  }
  &:active {
    background: #c7c7c7;
    border-color: #adadad;
    text-decoration: none;
    color: #111;
  }
  &.share-btn-sm {
    height: 20px;
    font-size: 10px;
    padding: 0 8px;
    line-height: 1.6;
  }
  &.share-btn-lg {
    height: 28px;
    font-size: 16px;
    line-height: 1.4;
  }
  .share-btn-text-sr {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    margin: -1px;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    border: 0;
  }
  &.share-btn-branded {
    color: #fff;
    &.share-btn-twitter {
      background: #55acee;
      border-color: #3ea1ec;
      &:hover, &:focus {
        background: #3ea1ec;
        border-color: #2795e9;
      }
      &:active {
        background: #2795e9;
        border-color: #1689e0;
      }
    }
    &.share-btn-facebook {
      background: #3b5998;
      border-color: #344e86;
      &:hover, &:focus {
        background: #344e86;
        border-color: #2d4373;
      }
      &:active {
        background: #2d4373;
        border-color: #263961;
      }
    }
    &.share-btn-googleplus {
      background: #dd4b39;
      color: #fff;
      border-color: #d73925;
      &:hover, &:focus {
        background: #d73925;
        border-color: #c23321;
      }
      &:active {
        background: #c23321;
        border-color: #ac2d1e;
      }
    }
    &.share-btn-tumblr {
      background: #35465c;
      color: #fff;
      border-color: #2c3a4c;
      &:hover, &:focus {
        background: #2c3a4c;
        border-color: #222d3c;
      }
      &:active {
        background: #222d3c;
        border-color: #19212b;
      }
    }
    &.share-btn-reddit {
      background: #ff4500;
      color: #fff;
      border-color: #e63e00;
      &:hover, &:focus {
        background: #e63e00;
        border-color: #cc3700;
      }
      &:active {
        background: #cc3700;
        border-color: #b33000;
      }
    }
    &.share-btn-linkedin {
      background: #0976b4;
      color: #fff;
      border-color: #08669c;
      &:hover, &:focus {
        background: #08669c;
        border-color: #075683;
      }
      &:active {
        background: #075683;
        border-color: #05466b;
      }
    }
    &.share-btn-delicious {
      background: #3399ff;
      color: #fff;
      border-color: #198cff;
      &:hover, &:focus {
        background: #198cff;
        border-color: #007fff;
      }
      &:active {
        background: #007fff;
        border-color: #0073e5;
      }
    }
    &.share-btn-stumbleupon {
      background: #eb4924;
      color: #fff;
      border-color: #e13b15;
      &:hover, &:focus {
        background: #e13b15;
        border-color: #ca3412;
      }
      &:active {
        background: #ca3412;
        border-color: #b22e10;
      }
    }
    &.share-btn-pinterest {
      background: #cc2127;
      color: #fff;
      border-color: #b61d23;
      &:hover, &:focus {
        background: #b61d23;
        border-color: #a01a1f;
      }
      &:active {
        background: #a01a1f;
        border-color: #8a161a;
      }
    }
  }
  &.share-btn-inverse {
    color: #eeeeee;
    background: #1f1f1f;
    border-color: #050505;
    &:hover, &:focus {
      background: #121212;
      border-color: #000000;
      color: #eeeeee;
    }
    &:active {
      background: #050505;
      border-color: #000000;
      color: #eeeeee;
    }
  }
  &.share-btn-twitter .share-btn-icon, &.share-btn-googleplus .share-btn-icon, &.share-btn-tumblr .share-btn-icon, &.share-btn-linkedin .share-btn-icon, &.share-btn-pinterest .share-btn-icon, &.share-btn-stumbleupon .share-btn-icon, &.share-btn-delicious .share-btn-icon {
    position: relative;
    top: 1px;
  }
  &.share-btn-more {
    .share-btn-icon {
      position: relative;
      top: 1px;
    }
    &.share-btn-lg .share-btn-icon {
      top: 2px;
    }
  }
  .share-btn-text {
    padding-left: 2px;
  }
}

@-moz-document url-prefix() {
  .share-btn {
    &.share-btn-twitter .share-btn-icon, &.share-btn-googleplus .share-btn-icon, &.share-btn-tumblr .share-btn-icon, &.share-btn-linkedin .share-btn-icon, &.share-btn-pinterest .share-btn-icon, &.share-btn-stumbleupon .share-btn-icon, &.share-btn-delicious .share-btn-icon, &.share-btn-more .share-btn-icon {
      top: 0;
    }
  }
}

/*********************************/

/* LOADING STYLE */

/*********************************/

.uil-ripple-css {
  background: none;
  position: fixed;
  left: 50%;
  top: 50%;
  width: 200px;
  height: 200px;
  margin-left: -100px;
  margin-top: -100px;
  div {
    position: absolute;
    top: 50%;
    left: 50%;
    margin: 0;
    width: 0;
    height: 0;
    opacity: 0;
    border-radius: 50%;
    border-width: 12px;
    border-style: solid;
    -ms-animation: uil-ripple 2s ease-out infinite;
    -moz-animation: uil-ripple 2s ease-out infinite;
    -webkit-animation: uil-ripple 2s ease-out infinite;
    -o-animation: uil-ripple 2s ease-out infinite;
    animation: uil-ripple 2s ease-out infinite;
    &:nth-of-type(1) {
      border-color: #afafb7;
    }
    &:nth-of-type(2) {
      border-color: #5cffd6;
      -ms-animation-delay: 1s;
      -moz-animation-delay: 1s;
      -webkit-animation-delay: 1s;
      -o-animation-delay: 1s;
      animation-delay: 1s;
    }
  }
}

/*********************************/

/* UPLOAD BUTTON STYLE */

/*********************************/

.upload-button {
  text-decoration: none;
  text-transform: uppercase;
  color: $gray !important;
  letter-spacing: 3px;
  font-weight: normal;
  font-size: 14px;
  display: inline-block;
  padding: 10px 15px;
  position: relative;
  z-index: 1;
  overflow: hidden;
  -webkit-transition: all 0.4s cubic-bezier(0.25, 0.1, 0.2, 1);
  transition: all 0.4s cubic-bezier(0.25, 0.1, 0.2, 1);
  &:before, &:after {
    position: absolute;
    content: "";
    display: block;
  }
  &:before {
    top: -120px;
    left: 50px;
    z-index: -1;
    width: calc(100% + 140px);
    height: 100px;
    -webkit-transition: all 0.4s cubic-bezier(0.25, 0.1, 0.2, 1);
    transition: all 0.4s cubic-bezier(0.25, 0.1, 0.2, 1);
    -webkit-transform: skew(70deg);
    transform: skew(70deg);
    background: #ec5f8b;
  }
  &:after {
    top: 0;
    left: 0;
    z-index: -2;
    width: calc(100% - 2px);
    height: calc(100% - 2px);
    border: 1px solid $gray-light;
  }
  &:hover:before {
    left: -50px;
    top: -20px;
  }
}