  0% {
    width: 0;
    height: 0;
    opacity: 0;
    margin: 0 0 0 0;
  }

  33% {
    width: 44%;
    height: 44%;
    margin: -22% 0 0 -22%;
    opacity: 1;
  }

  100% {
    width: 88%;
    height: 88%;
    margin: -44% 0 0 -44%;
    opacity: 0;
  }
}


